import { _args } from '../import';
import { THREE } from '../4.0';

function evalPoints() {
    var args = _args(arguments);
    let typ = args.str[0].toUpperCase();
    let obj, rnd, cam;

    if(typ === 'SCREEN') {
        obj = args.obj[0];
        rnd = args.obj[1];
        cam = args.obj[2];

        let vec = new THREE.Vector3();
        vec = vec.setFromMatrixPosition(obj.matrixWorld);
        vec.project(cam);

        let widthHalf = rnd.domElement.width / 2;
        let heightHalf = rnd.domElement.height / 2;

        vec.x = + (vec.x * widthHalf) + widthHalf;
        vec.y = - (vec.y * heightHalf) + heightHalf;
        vec.z = 0;

        return vec;
    }
};

export {
    evalPoints
};