import { Component } from 'react';
import './App.css';
import LoadingScreen from './bin/logo/arimac';
import { TrinityEngine, THREE } from './engine/4.0/';

let WebXR = TrinityEngine.webReality.WebXR;

// ====================================================================================

class App extends Component {

	componentDidMount() {
		onLoad();
	};

	render() {
		return (
			<div>
				<div className="arimac_load">
					<div className="click_text">Tap Anywhere to Start</div>
				</div>
				<div className="arimac_logo arimac_logo_white"></div>
				<div className="cameraRoot"></div>
			</div>
		);
	};

};

// ====================================================================================

let isStarted = false;
let modules = null;
let model = null;

function onLoad() {
	// start loading screen
	LoadingScreen.start('#000', function() {
		// load model
		TrinityEngine.loadModel('models/model.fbx', function(obj) {
			setupModel(obj);
			model = obj;

			if(iOS()) {
				loadGoogleMV();
				return;
			}

			// show tap text
			document.querySelector('.click_text').style.color = '#FFF';
			// user event listener
			window.addEventListener('click', function() {
				if(isStarted === false) {

					WebXR.checkAvailability(
						loadXRApp,      // web-xr
						loadGoogleMV // model-viewer
					);
				}
			});
		});
	});
};

function iOS() {
	return [
	  'iPad Simulator',
	  'iPhone Simulator',
	  'iPod Simulator',
	  'iPad',
	  'iPhone',
	  'iPod'
	].includes(navigator.platform)
	// iPad on iOS 13 detection
	|| (navigator.userAgent.includes("Mac") && "ontouchend" in document)
}

function loadGoogleMV() {
	isStarted = true;

	//document.querySelector('#root').innerHTML = '<model-viewer src="models/mva/model.glb" ar ar-scale="fixed" camera-controls alt="A 3D model of an astronaut" skybox-image="models/mv/aircraft_workshop_01_1k.hdr" ios-src="models/mva/model.usdz" xr-environment></model-viewer>';

	document.querySelector('#root').innerHTML = `<div class="demo" style="background: linear-gradient(#e60000, #e60000); overflow-x: hidden;">
	<model-viewer src="models/mva/model.glb" ar ar-scale="fixed" camera-controls alt="A 3D model of an astronaut" ios-src="models/mva/model.usdz" xr-environment></model-viewer>
  </div>`;


	var e = document.createElement('script');
	e.src = 'https://unpkg.com/@google/model-viewer@1.8.0/dist/model-viewer-umd.js';
	document.head.appendChild(e);

};






















// ====================================================================================

function loadModelViewer() {
	isStarted = true;
	let three = TrinityEngine.createScene('#333');
	document.querySelector('#root').appendChild(three.renderer.domElement);

	three.scene.add(model);
	
	model.scale.set(0.0031, 0.0031, 0.0031);
	model.position.set(0, -0.6, 0);

	three.controls.minPolarAngle = Math.PI / 2;
	three.controls.maxPolarAngle = Math.PI / 2;
	three.controls.enableZoom = false;
	three.renderer.shadowMap.enabled = false;

	three.renderer.physicallyCorrectLights = true;

	let light = TrinityEngine.createLight('DIRECT', '#fff', 2.5);

	light.castShadow = true;
	light.shadow.mapSize.width = 1024;
	light.shadow.mapSize.height = 1024;
	light.shadow.camera.far = 1000;

	three.scene.add(light);

	LoadingScreen.end();

	window.addEventListener('resize', function() {
		TrinityEngine.setupScene(three.renderer, three.camera)
	});

};

// ====================================================================================

function loadXRApp() {
	let camRoot = document.querySelector('.cameraRoot');
	WebXR.requestSession(camRoot, function(obj) {
		modules = obj; renderStart();


		let light = TrinityEngine.createLight('DIRECT', '#fff', 1.5);

		light.castShadow = true;
		light.shadow.mapSize.width = 1024;
		light.shadow.mapSize.height = 1024;
		light.shadow.camera.far = 1000;

		modules.renderer.shadowMap.enabled = false;


		model.scale.set(0, 0, 0);
		modules.scene.add(model);
		modules.scene.add(light);

		model.position.set(0, -0.3, -1.3);

		TrinityEngine.tweenObject(model, {
			scale : { x : 0.001, y : 0.001, z : 0.001 }
		}, 1500);

	});
};

let material = new THREE.MeshPhongMaterial({
	color : new THREE.Color('#FFF'),
	shininess : 25
});

function setupModel(mdl) {
	TrinityEngine.findByType(mdl, 'Mesh', true).forEach(function(mesh) {
		mesh.material = material;
		mesh.material.needsUpdate = true;
		mesh.castShadow = true;
		mesh.receiveShadow = true;
	});
};

function renderStart() {
	WebXR.renderLoop(modules, function() {})
};

function render() {
	requestAnimationFrame(render);
	if(modules === null) { return; }
	if(model === null) { return; }
	model.rotation.set(
		model.rotation.x,
		model.rotation.y + speed,
		model.rotation.z
	);
	checkRot(model.rotation.y % (Math.PI * 2));
};

let state = 'U';

let speed = 0.012;

function checkRot(r) {
	console.log(r);
	if(r < 0.2 || r > 6.1) {
		if(state === 'A') { return; } else { state = 'A'; }
		speed = 0.003;
	}else if(r < 4.7 && r > 4.5) { // light
		if(state === 'B') { return; } else { state = 'B'; }
		speed = 0.003;
	} else {
		if(state === 'C') { return; } else { state = 'C'; }
		speed = 0.022;
	}
};

let light = new THREE.AmbientLight('#FFF', 0.2, 1);

render();


// ====================================================================================

export default App;